import '../FooterLinks/FooterLinks.scss'

const FooterLinks = ({ links = [] }) => {
    return(
        <div className='footerLinks'>
            <ul>
                {links.map((item, index) => (
                    <li key={index} className='linksListItem'>
                        <a href={item.link}>{item.text}</a>
                    </li>
                ))} 
            </ul>
        </div>
    )
}

export default FooterLinks;