import '../Footer/Footer.scss'
import FooterMain from '../FooterMain/FooterMain';
import FooterTitle from '../FooterTitle/FooterTitle';

import whatsappIcon from '../../assets/images/icons/whatsappIcon.png';
import telegramIcon from '../../assets/images/icons/telegramIcon.png';
import vkIcon from '../../assets/images/icons/vkIcon.png';
import teletypeIcon from '../../assets/images/icons/teletypeIcon.png';
import FooterInfo from '../FooterInfo/FooterInfo';

const Footer = ({ footerMenuItems = [] }) => {
    const socials = [
        { 
            icon: whatsappIcon, 
            link: "", 
            alt: "WhatsApp" 
        },
        { 
            icon: telegramIcon, 
            link: "", 
            alt: "Telegram" 
        },
        { 
            icon: vkIcon, 
            link: "", 
            alt: "VK" 
        },
        { 
            icon: teletypeIcon, 
            link: "", 
            alt: "Teletype" 
        }
    ];

    const links = [
        {
            link: '',
            text: '  '
        },
        {
            link: '',
            text: ''
        },
        {
            link: '',
            text: ' '
        },
        {
            link: '',
            text: ' '
        }
    ]

    return(
        <footer className='footer'>
            <div className='container'>
                <FooterTitle />
                <FooterMain socials={socials} footerMenuItems={footerMenuItems} links={links}/>
                <FooterInfo />
            </div>
        </footer>
    )
}

export default Footer;